@import "helpers/all";

.color-main {
	color: $mainColor;
	@include active {
		color: $mainColor; } }
//btns
.color-btn-main {
	color: $white;
	background: $mainColor;
	@include active {
		color: $white;
		background: $mainColorActive; } }

//header
.color-header {
	color: $white;
	background: $mainColor; }

//modal
.color-modal-header {
	color: $white;
	background: $mainColor; }

//title
.color-title-main {
	color: $mainColor; }

//pin screen
.color-circle-main {
	border-color: $mainColor;
	&.is-active {
		background: $mainColor; } }
